import { defineStore } from 'pinia'
import { ref } from 'vue'

export const useOsStore = defineStore('os', () => {
    const windows = ref([]);
    const activeWindowId = ref(null);
    let nextZIndex = 100;
  
    // –û–ë–™–Ø–í–õ–Ø–ï–ú –§–£–ù–ö–¶–ò–Æ –î–û –¢–û–ì–û –ö–ê–ö –û–ù–ê –ò–°–ü–û–õ–¨–ó–£–ï–¢–°–Ø
    const activateWindow = (windowId) => {
        const window = windows.value.find(w => w.id === windowId);
        if (window) {
            // –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –∞–∫—Ç–∏–≤–Ω–æ–µ –æ–∫–Ω–æ
            activeWindowId.value = windowId;
      
            // –û–±–Ω–æ–≤–ª—è–µ–º z-index –¥–ª—è –≤—Å–µ—Ö –æ–∫–æ–Ω
            windows.value.forEach(w => {
                if (w.id === windowId) {
                    // –ê–∫—Ç–∏–≤–Ω–æ–º—É –æ–∫–Ω—É –¥–∞–µ–º —Å–∞–º—ã–π –≤—ã—Å–æ–∫–∏–π z-index
                    w.zIndex = nextZIndex++;
                }
            });
        }
    }
  
    const openWindow = (appName) => {
        const newWindow = {
            id: Date.now().toString(),
            appName: appName,
            title: `${appName} - –ü—Ä–∏–ª–æ–∂–µ–Ω–∏–µ`,
            isMinimized: false,
            isMaximized: false,
            zIndex: nextZIndex++
        };
    
        windows.value.push(newWindow);
        activateWindow(newWindow.id);
    
        return newWindow.id
    };
  
    const closeWindow = (windowId) => {
        const index = windows.value.findIndex(w => w.id === windowId);
        if (index !== -1) {
            windows.value.splice(index, 1);
        }
    };
  
    const minimizeWindow = (windowId) => {
        const window = windows.value.find(w => w.id === windowId);
        if (window) {
            window.isMinimized = true;
        }
    };
  
    const restoreWindow = (windowId) => {
        const window = windows.value.find(w => w.id === windowId);
        if (window) {
            window.isMinimized = false;
            activateWindow(windowId);
        }
    };
  
    const toggleMaximizeWindow = (windowId) => {
        const window = windows.value.find(w => w.id === windowId);
        if (window) {
            window.isMaximized = !window.isMaximized;
            activateWindow(windowId);
        }
    }
  
    return {
        windows,
        activeWindowId,
        openWindow,
        closeWindow,
        minimizeWindow,
        restoreWindow,
        toggleMaximizeWindow,
        activateWindow // –≠–∫—Å–ø–æ—Ä—Ç–∏—Ä—É–µ–º
    }
})